import React, { Component } from 'react';
import Event from './Event'
import {CautionAlert} from './Alert'

class EventList extends Component {

  state = {
    infoText: ''
  }

  componentDidMount() {
    const offline = localStorage.getItem('offline')
    if (offline) {
      this.setState({
        infoText: 'WARNING: App is running offline. Events may not be up to date. Connect to the internet to get the most up to date information.',
      });
    } else {
      this.setState({
        infoText: '',
      });
    }
  }

  render() {
    
    return (
      <ul className="EventList">
      <CautionAlert text={this.state.infoText} />
        {this.props.events.map(event =>
          <li key={event.id}>
            <Event event={event} />
          </li>
        )}
      </ul>
    );
  }
}

export default EventList;